<template>
  <v-select
    dense
    :value="lang"
    :items="languages"
    @change="onSelectLang"
    :label="$t('config.language')"
  />
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
  name: "LangSelector",

  data() {
    return {
      languages: [
        {
          text: "",
          value: "bn",
        },
        {
          text: "English",
          value: "en",
        },
        {
          text: "Franais",
          value: "fr",
        },
        {
          text: "",
          value: "ko",
        },
        {
          text: "Portugus (Brazil)",
          value: "pt-BR",
        },
        {
          text: "Trke",
          value: "tr",
        },
        {
          text: "",
          value: "zh-CN",
        },
      ],
    };
  },

  computed: {
    ...mapState({
      lang: (state) => state.config.lang,
    }),
  },

  methods: {
    ...mapMutations("config", ["selectLang"]),
    onSelectLang(lang) {
      this.$i18n.locale = lang;
      this.selectLang(lang);
    },
  },
};
</script>
